body {
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  line-height: 21px;
  color: #1d1613;
  background-color: #ffffff; }

.img {
  max-width: 100%;
  height: auto; }

.wrap-bg {
  background-color: #000000; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.main-header {
  padding-top: 30px;
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 1024px;
  max-width: 1366px;
  width: 100%;
  padding-left: 7%;
  padding-right: 7%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center; }

.main-header__menu-button {
  display: none; }

.main-header__cart-button {
  display: none; }

.main-header__user {
  width: 33%;
  box-sizing: border-box;
  display: flex;
  min-width: 122px;
  justify-content: flex-end;
  padding-right: 12px; }

.main-header__user-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #1d1613;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  margin-right: 35px; }

.main-header__user-link:last-child {
  margin-right: 0; }

.main-header__user-link span {
  margin-left: 5px; }

.main-header__user-link:hover {
  opacity: 70%; }

.main-header__search {
  width: 33%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  min-width: 169px;
  padding-left: 30px; }

.main-header__search-svg {
  top: 0;
  left: 0;
  width: 14px;
  height: 14px;
  position: absolute; }

.main-header__search input {
  outline: none;
  box-sizing: border-box;
  width: 140px;
  height: 15px;
  border: 1px solid white; }

.main-header__search input::placeholder {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #1d1613;
  font-family: "Poppins", "Arial", sans-serif; }

.main-header__logo-link {
  width: 33%;
  display: flex;
  justify-content: center;
  align-items: center; }

.main-header__search input:hover,
.main-header__search input:active {
  border-color: #1d1613;
  opacity: 75%; }

.main-header__user-link:active {
  opacity: 50%; }

.main-header__nav {
  margin: 0 auto;
  margin-top: 30px;
  box-sizing: border-box;
  border-top: 1px solid #f6f5f4;
  border-bottom: 1px solid #f6f5f4;
  padding-top: 15px;
  padding-bottom: 15px;
  min-height: 50px;
  width: 100%;
  margin-bottom: 10px; }

.main-header__nav-item {
  margin-right: 35px; }

.main-header__nav-item:hover {
  opacity: 70%; }

.main-header__nav-item:active {
  opacity: 50%; }

.main-header__nav-list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  min-width: 493px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row; }

.main-header a {
  text-decoration: none;
  list-style: none;
  color: #1d1613; }

.main-header__for-customers {
  display: none; }

.main-header__nav-item:last-child {
  margin-right: 0; }

@media (max-width: 1024px) {
  .main-header--open {
    background-color: #a87b62;
    color: #ffffff;
    padding-bottom: 80px; }
  .main-header--open .main-header__logo-link path {
    fill: #ffffff; }
  .main-header {
    position: relative;
    min-width: 768px;
    padding-left: 5, 8%;
    padding-right: 5, 8%;
    align-items: stretch; }
  .main-header a {
    color: #ffffff; }
  .main-header__nav {
    border: none;
    margin-top: 0;
    box-sizing: border-box;
    width: 100%; }
  .main-header__nav-list {
    box-sizing: border-box;
    flex-direction: column;
    align-items: flex-start;
    font-size: 24px;
    line-height: 36px; }
  .main-header__nav-item {
    margin-bottom: 30px; }
  .main-header__for-customers {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 18px;
    line-height: 23px;
    color: rgba(255, 255, 255, 0.75); }
  .main-header__for-customers a {
    margin-bottom: 25px;
    color: rgba(255, 255, 255, 0.75); }
  .main-header__logo-link {
    width: 100%;
    order: -1;
    margin-bottom: 55px; }
  .main-header__search {
    padding-left: 0;
    width: 100%;
    margin-bottom: 55px; }
  .main-header__search-svg {
    left: 6%;
    top: 35%;
    width: 18px;
    height: 16px; }
  .main-header__search-svg path {
    fill: white; }
  .main-header__search input {
    width: 100%;
    height: 45px;
    border-radius: 21px;
    background-color: rgba(255, 255, 255, 0.15);
    border-color: transparent;
    padding-left: 50px; }
  .main-header__search input:hover,
  .main-header__search input:focus {
    border-color: white; }
  .main-header__search input::placeholder {
    color: transparent; }
  .main-header__user {
    order: 1;
    justify-content: flex-start;
    width: 100%;
    font-size: 18px;
    line-height: 23px; }
  .main-header__user-link {
    font-size: 18px;
    line-height: 23px; }
  .main-header__user-link--cart {
    display: none; }
  .main-header__menu-button {
    display: block;
    position: absolute;
    top: 5%;
    left: 5%; }
  .main-header__cart-button {
    display: block;
    position: absolute;
    top: 5%;
    right: 5%; }
  .main-header--open path {
    stroke: white; } }

@media (max-width: 768px) {
  .main-header {
    min-width: 320px;
    margin: 0 auto;
    padding-left: 4, 6%;
    padding-right: 4, 6%; } }

.main-footer {
  min-width: 1024px;
  max-width: 1366px;
  box-sizing: border-box;
  padding-top: 100px;
  padding-bottom: 100px;
  padding-left: 7%;
