  margin: 0 auto;
  display: flex;
  background-color: #000000;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  align-items: center;
  justify-content: space-between; }

@media (max-width: 1024px) {
  .main-footer {
    padding-left: 5, 8%;
    padding-right: 5, 8%;
    flex-wrap: wrap;
    min-width: 768px;
    max-width: 1024px; } }

@media (max-width: 768px) {
  .main-footer {
    padding-left: 4, 6%;
    padding-right: 4, 6%;
    min-width: 320px;
    max-width: 768px; } }

.company-info {
  display: flex;
  flex-direction: column; }

.company-info__link {
  margin-bottom: 25px;
  text-decoration: none;
  list-style: none;
  color: #ffffff; }

.company-info__link:hover {
  opacity: 70%; }

.company-info__link:active {
  opacity: 50%; }

.company-info__link:last-child {
  margin-bottom: 0; }

@media (max-width: 768px) {
  .company-info {
    width: 100%;
    margin-bottom: 40px; } }

.subscribe {
  border-bottom: 1px solid #ffffff; }

.subscribe__header {
  list-style: none;
  margin: 0;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 34px;
  line-height: 44px;
  color: #ffffff; }

.subscribe__text {
  margin-bottom: 40px;
  font-size: 16px;
  line-height: 21px;
  color: #9b9997; }

.subscribe input {
  box-shadow: none;
  outline: none;
  margin: 0;
  padding: 0;
  border: 1px solid transparent;
  background-color: transparent;
  color: #ffffff; }

.subscribe__link {
  position: relative;
  display: block;
  min-height: 30px;
  margin-left: auto;
  padding-right: 40px;
  text-decoration: none;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #ffffff; }

.subscribe__link:hover {
  opacity: 70%; }

.subscribe__link:active {
  opacity: 50%; }

.subscribe__link::after {
  top: 5px;
  left: 65px;
  content: "";
  position: absolute;
  width: 29px;
  height: 9px;
  background-image: url("../../img/arrow-right-icon.svg");
  background-position: 0 0;
  background-repeat: no-repeat; }

.subscribe__sign-up {
  display: flex; }

@media (max-width: 1024px) {
  .subscribe {
    margin-bottom: 40px;
    width: 100%; } }

.order-info {
  display: flex;
  flex-direction: column; }

.order-info__link {
  text-decoration: none;
  list-style: none;
  color: #ffffff;
  margin-bottom: 25px; }

.order-info__link:hover {
  opacity: 70%; }

.order-info__link:active {
  opacity: 50%; }

.order-info__link:last-child {
  margin-bottom: 0; }

.social {
  display: flex;
  flex-direction: column; }

.social__link {
  position: relative;
  margin-bottom: 25px;
  padding-left: 30px;
  text-decoration: none;
  list-style: none;
  color: #ffffff; }

.social__link:hover {
  opacity: 70%; }

.social__link:active {
  opacity: 50%; }

.social__link:last-child {
  margin-bottom: 0; }

.social__link::before {
  content: "";
  top: 3px;
  left: 0;
  position: absolute;
  background-position: 0 0;
  background-repeat: no-repeat; }

.social__link--instagram::before {
  width: 15px;
  height: 15px;
  background-image: url("../../img/instagram-icon.svg"); }

.social__text {
  margin: 0; }

.social__link--facebook::before {
  width: 8px;
  height: 15px;
  background-image: url("../../img/facebook-icon.svg"); }

.social__link--pininterest::before {
  width: 15px;
  height: 15px;
  background-image: url("../../img/pininterest-icon.svg"); }

.social__link--twitter::before {
  width: 15px;
  height: 13px;
  background-image: url("../../img/twitter-icon.svg"); }

@media (max-width: 768px) {
  .social {
    width: 100%;
    flex-direction: row; }
  .social__text {
    display: none; }
  .social__link {
    padding-left: 0;
    min-width: 30px;
    margin-bottom: 0;
    margin-right: 25px; }
  .social__link:last-child {
    margin-right: 0; }
  .social__link--facebook::before {
    width: 12px;
    height: 21px;
    background-image: url("../../img/facebook-icon__mobile.svg"); }
  .social__link--pininterest::before {
    width: 20px;
    height: 21px;
    background-image: url("../../img/pininterest-icon__mobile.svg"); }
  .social__link--twitter::before {
    width: 20px;
    height: 18px;
    background-image: url("../../img/twitter__mobile.svg"); }
  .social__link--instagram::before {
    width: 20px;
    height: 21px;
    background-image: url("../../img/instagram-icon__mobile.svg"); } }
