.main-footer {
  min-width: 1024px;
  max-width: 1366px;
  box-sizing: border-box;
  padding-top: 100px;
  padding-bottom: 100px;
  padding-left: 7%;
  padding-right: 7%;
  margin: 0 auto;
  display: flex;
  background-color: #000000;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  align-items: center;
  justify-content: space-between;
}




@media (max-width: $desktop-width) {
  .main-footer {
    padding-left: 5, 8%;
    padding-right: 5, 8%;
    flex-wrap: wrap;
    min-width: 768px;
    max-width: 1024px;
  }

}

@media (max-width: $tablet-width) {
  .main-footer {
    padding-left: 4, 6%;
    padding-right: 4, 6%;
    min-width: 320px;
    max-width: 768px;
  }

}
